/**
    List of Tricky inputs:

    0. []
    0. {\r\n  \"XapDebug\": 1}
    1. { \\\"a\\\" : 2}
    2. { "a" : 2};{ "b" : 3}
    3. { "a" : 2};{ \"b\" : 3};{ \\\"c\\\" : \\\"x\\\"}
    4. {  \"XapDebug\": [ {      \"SubstrateSearch.FSL.Plugins.ModernResultType.ModernResultTypeDataBuilderPlugin\": \"ModernResultTypeCollections site collection id= ModernResultTypeCollections fetched from SearchSetting=[{\\\"pureBondInstance\\\":{\\\"ModernResultTypes\\\":null,\\\"Scope\\\":3},\\\"ModernResultTypes\\\":[],\\\"Scope\\\":3,\\\"IsReadOnly\\\":false}]\"    }    ]}
 */

function breakIntoJsonStrings(input) {
    let jsonSubStringFound = false;
    let jsonStrings = [];
    let unparsedIndex = 0;
    while (unparsedIndex < input.length) {
        let startIndex = input.indexOf('{', unparsedIndex);
        if (startIndex === -1) {
            jsonStrings.push(input.substring(unparsedIndex, input.length));
            unparsedIndex = input.length;
        } else if (startIndex > unparsedIndex) {
            jsonStrings.push(input.substring(unparsedIndex, startIndex));
            unparsedIndex = startIndex;
        } else { // startIndex === unparsedIndex
            let balance = 0;
            let endIndex = input.length - 1; // Default to end of input string

            for (let i = startIndex; i < input.length; i++) {
                if (input[i] === '{') balance++;
                if (input[i] === '}') balance--;
    
                if (balance === 0) { // Found a complete JSON object
                    jsonSubStringFound = true;
                    endIndex = i;
                    break;
                }
            }
            jsonStrings.push(input.substring(startIndex, endIndex + 1));
            unparsedIndex = endIndex + 1;
        }
    }

    //validation
    //sum of all json strings should be equal to input length
    let totalLength = 0;
    for (let i = 0; i < jsonStrings.length; i++) {
        totalLength += jsonStrings[i].length;
    }
    if(totalLength !== input.length) {
        console.log('breakIntoJsonStrings() - Error: Total length of all JSON strings does not match input length');
        console.log('input:', input);
        console.log('jsonStrings:', jsonStrings);
    }

    if(!jsonSubStringFound || jsonStrings.length === 1) {
        return null;
    }

    return jsonStrings;
}

function unescapeString(input) {
    let unescapedString = input
      .replace(/\\\\/g, '\\')   // Replace escaped backslashes (\\ -> \)
      .replace(/\\"/g, '"')      // Replace escaped double quotes (\" -> ")
      .replace(/\\r\\n/g, '\r\n') // Replace escaped \r\n with actual line breaks
      .replace(/\\n/g, ' ')      // Replace escaped newlines (\n -> newline)
      .replace(/\\r/g, ' ')      // Replace escaped carriage return (\r -> carriage return)
      .replace(/\\t/g, ' ');     // Replace escaped tabs (\t -> tab)
  
    console.log('unescapedString()', input, unescapedString);
    return unescapedString;
  }

function isBase64(str) {
  try {
    return btoa(atob(str)) === str;
  } catch (e) {
    return false;
  }
}

function parseJson(str) {
  try {
    const jsonObj = JSON.parse(str);
    console.log("parseJson()", str, jsonObj);
    return jsonObj;
  } catch (e) {
    console.log("Not a json", str);
    return null;
  }
}

function decodeBase64Gzip(base64String) {
    try {
      //const compressedBuffer = Uint8Array.from(atob(base64String), (c) => c.charCodeAt(0));
      const decompressed = base64String//pako.ungzip(compressedBuffer, { to: "string" });
      return decompressed;
    } catch (e) {
      return null; // Return null if not valid Base64-encoded Gzip
    }
  }

function decodeArray(inputArray) {
    let updated = false;
    const updatedArray = [];
    for (let i = 0; i < inputArray.length; i++) {
        const decodedValue = decodeAny(inputArray[i]);
        if (decodedValue != null) {
            updated = true;
            updatedArray.push(decodedValue);
        } else {
            updatedArray.push(inputArray[i]);
        }
    }
    if (updated) {
        return updatedArray;
    }
    return null;
}

function decodeObject(inputObject) {
    let updated = false;
    const updatedObject = {};
    for (const key in inputObject) {
        if (Object.prototype.hasOwnProperty.call(inputObject, key)) {
            const decodedValue = decodeAny(inputObject[key]);
            if (decodedValue != null) {
                updated = true;
                updatedObject[key] = decodedValue;
            } else {
                updatedObject[key] = inputObject[key];
            }
        }
    }
    if (updated) {
        return updatedObject;
    }
    return null;
}

function decodeString(_input) {
    let decodedValue = _input;

    if (isBase64(decodedValue)) {
        try {
          const decodedBase64 = atob(decodedValue);
          if(decodedBase64 !== decodedValue)
            decodedValue = decodedBase64
        } catch (e) {
            // base64 decode failed;    
        }
    }

    const gzipDecoded = decodeBase64Gzip(decodedValue);
    if (gzipDecoded !== null) {
        decodedValue = gzipDecoded;
    }

    const unescapedString = unescapeString(decodedValue);
    if(unescapedString !== decodedValue) {
        decodedValue = unescapedString;
    }

    const jsonObject = parseJson(decodedValue);
    if (jsonObject !== null) {
        return jsonObject; //if json decoding is successful, return the decoded json object
    }

    const strArray = breakIntoJsonStrings(decodedValue);
    if(strArray !== null) {
        return strArray;
    }

    if(decodedValue !== _input) {
        return decodedValue;
    }
    return null;
}

/** returns null if no update is made to input */
function decodeAny(input) {
    if(typeof input === 'string') {
        return decodeString(input);
    } else if (Array.isArray(input)) {
        return decodeArray(input);
    } else if (typeof input === 'object') {
        return decodeObject(input);
    }
    return null;
}

function recursiveDecode(input) {
    console.log(`recursiveDecode(${input})`);
    let decodedValue = decodeAny(input);
    let returnValue = input;
    while(decodedValue !== null) {
        returnValue = decodedValue;
        decodedValue = decodeAny(decodedValue);
    }
    return returnValue;
}

export default recursiveDecode;
