<template>
    <div> 
      <h1>This is the Decoder View</h1>
      <v-textarea v-model="inputString" label="Input String" variant="outlined" autofocus="true"></v-textarea>
      <v-btn @click="decodeString">Decode</v-btn>
      <v-textarea v-model="decodedString" label="Decoded String" variant="outlined" readonly auto-grow></v-textarea>
    </div>
</template>
  
<script>
import { defineComponent } from 'vue';
import recursiveDecode from '../scripts/string-decoder';

export default defineComponent({
    name: 'DecoderView',
    data() {
        return {
            inputString: '',
            decodedString: ''
        };
    },
    methods: {
        decodeString() {
            const decoded = recursiveDecode(this.inputString);
            console.log(decoded);
            this.decodedString = JSON.stringify(decoded, null, 2);
        }
    }
});
</script>